// src/components/home/Navbar.js
'use client'

import React, { useState } from 'react'
import Image from 'next/image'
import Link from 'next/link'
import {
	Search,
	ShoppingCart,
	Menu,
	User,
	Package,
	LogOut,
	X,
} from 'lucide-react'

// Shadcn UI komponentlari (import yo'llarini loyihangizga moslang)
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import {
	DropdownMenu,
	DropdownMenuContent,
	DropdownMenuItem,
	DropdownMenuLabel,
	DropdownMenuSeparator,
	DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu'
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar'

// Kategoriyalar ro'yxati (Assignment Brief asosida)
const categories = [
	'Oshxona jihozlari',
	'Tozalash texnikasi',
	'Isitish va sovutish',
	'Shaxsiy parvarish',
	'Aqlli uy',
	'Aksiyalar',
]

const Navbar = () => {
	// Foydalanuvchi holati (Login qilingan yoki yo'qligini tekshirish uchun mock data)
	const [isLoggedIn, setIsLoggedIn] = useState(false) // True qilib ko'ring
	const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)

	// Ranglar (Logo asosida):
	// Asosiy: Blue-900 (#1e3a8a)
	// Urg'u: Orange-500 (#f97316)

	return (
		<header className='sticky top-0 z-50 w-full border-b bg-white shadow-sm'>
			<div className='container mx-auto px-4 h-20 flex items-center justify-between gap-4'>
				{/* --- 1. LOGO VA KATALOG --- */}
				<div className='flex items-center gap-6'>
					{/* Logo */}
					<Link href='/' className='flex-shrink-0'>
						<Image
							src='/logo.png'
							alt='Tech House Logo'
							width={150}
							height={50}
							className='object-contain h-12 w-auto'
							priority
						/>
					</Link>

					{/* Desktop Catalog Button */}
					<div className='hidden md:block'>
						<DropdownMenu>
							<DropdownMenuTrigger asChild>
								<Button
									variant='outline'
									className='bg-blue-900 text-white hover:bg-blue-800 hover:text-white border-none flex items-center gap-2'
								>
									<Menu className='h-4 w-4' />
									Katalog
								</Button>
							</DropdownMenuTrigger>
							<DropdownMenuContent className='w-56 mt-2'>
								<DropdownMenuLabel>Bo'limlar</DropdownMenuLabel>
								<DropdownMenuSeparator />
								{categories.map((cat, index) => (
									<DropdownMenuItem key={index} className='cursor-pointer'>
										{cat}
									</DropdownMenuItem>
								))}
							</DropdownMenuContent>
						</DropdownMenu>
					</div>
				</div>

				{/* --- 2. QIDIRUV (SEARCH) --- */}
				<div className='hidden md:flex flex-1 max-w-lg items-center relative'>
					<Input
						type='text'
						placeholder='Mahsulotlarni qidirish...'
						className='pr-10 border-blue-200 focus-visible:ring-blue-900'
					/>
					<Search className='absolute right-3 h-5 w-5 text-gray-400' />
				</div>

				{/* --- 3. O'NG TOMON (NAV ACTIONS) --- */}
				<div className='hidden md:flex items-center gap-6'>
					{/* Buyurtmalarim */}
					<Link
						href='/orders'
						className='flex flex-col items-center text-gray-600 hover:text-blue-900 transition text-xs font-medium'
					>
						<Package className='h-6 w-6 mb-1' />
						Buyurtmalar
					</Link>

					{/* Savat */}
					<Link
						href='/cart'
						className='flex flex-col items-center text-gray-600 hover:text-blue-900 transition text-xs font-medium relative'
					>
						<div className='relative'>
							<ShoppingCart className='h-6 w-6 mb-1' />
							<span className='absolute -top-1 -right-2 bg-orange-500 text-white text-[10px] font-bold px-1.5 py-0.5 rounded-full'>
								3
							</span>
						</div>
						Savat
					</Link>

					{/* Profil / Kirish */}
					{isLoggedIn ? (
						<DropdownMenu>
							<DropdownMenuTrigger asChild>
								<Avatar className='cursor-pointer border-2 border-blue-900'>
									<AvatarImage src='https://github.com/shadcn.png' />
									<AvatarFallback>TH</AvatarFallback>
								</Avatar>
							</DropdownMenuTrigger>
							<DropdownMenuContent align='end'>
								<DropdownMenuLabel>Mening Hisobim</DropdownMenuLabel>
								<DropdownMenuSeparator />
								<DropdownMenuItem>Profil sozlamalari</DropdownMenuItem>
								<DropdownMenuItem>To'lovlar</DropdownMenuItem>
								<DropdownMenuSeparator />
								<DropdownMenuItem
									onClick={() => setIsLoggedIn(false)}
									className='text-red-500'
								>
									<LogOut className='mr-2 h-4 w-4' /> Chiqish
								</DropdownMenuItem>
							</DropdownMenuContent>
						</DropdownMenu>
					) : (
						<Button
							onClick={() => setIsLoggedIn(true)} // Test uchun login bo'lish
							className='bg-orange-500 hover:bg-orange-600 text-white font-semibold shadow-md'
						>
							<User className='mr-2 h-4 w-4' />
							Kirish
						</Button>
					)}
				</div>

				{/* --- 4. MOBILE MENU TOGGLE --- */}
				<div className='md:hidden flex items-center gap-4'>
					<Link href='/cart' className='relative text-gray-700'>
						<ShoppingCart className='h-6 w-6' />
						<span className='absolute -top-2 -right-2 bg-orange-500 text-white text-[10px] font-bold px-1.5 py-0.5 rounded-full'>
							3
						</span>
					</Link>
					<Button
						variant='ghost'
						size='icon'
						onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
					>
						{isMobileMenuOpen ? <X /> : <Menu />}
					</Button>
				</div>
			</div>

			{/* --- MOBILE MENU CONTENT --- */}
			{isMobileMenuOpen && (
				<div className='md:hidden border-t bg-gray-50 p-4 absolute w-full left-0 shadow-lg'>
					<div className='flex flex-col gap-4'>
						{/* Mobile Search */}
						<div className='relative w-full'>
							<Input
								type='text'
								placeholder='Qidirish...'
								className='w-full bg-white'
							/>
							<Search className='absolute right-3 top-2.5 h-5 w-5 text-gray-400' />
						</div>

						{/* Mobile Categories */}
						<div className='grid grid-cols-2 gap-2'>
							{categories.map((cat, idx) => (
								<Button
									key={idx}
									variant='outline'
									className='justify-start text-sm'
								>
									{cat}
								</Button>
							))}
						</div>

						<div className='border-t pt-4 flex flex-col gap-2'>
							<Button variant='ghost' className='justify-start'>
								<Package className='mr-2 h-4 w-4' /> Buyurtmalarim
							</Button>
							{isLoggedIn ? (
								<Button
									variant='destructive'
									onClick={() => setIsLoggedIn(false)}
									className='justify-start'
								>
									<LogOut className='mr-2 h-4 w-4' /> Chiqish
								</Button>
							) : (
								<Button
									onClick={() => setIsLoggedIn(true)}
									className='bg-orange-500 w-full text-white'
								>
									Kirish
								</Button>
							)}
						</div>
					</div>
				</div>
			)}
		</header>
	)
}

export default Navbar
